import { Injectable } from '@angular/core';
import { PAGES_DATA } from "@core/configs/data.config";
import { IPagesData } from "@shared/models/pages-data.model";
import { IRoomPage } from "@shared/models/room-page.model";

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private readonly _pagesData: IPagesData;

  constructor() {
    this._pagesData = PAGES_DATA;
  }

  get pagesData(): IPagesData {
    return this._pagesData;
  }

  get pagesNameList(): Array<string> {
    return this._pagesData.pages.map(page => page.name);
  }

  getPageStepIndex(pageName: string): number {
    return this.pagesNameList.indexOf(pageName) + 1;
  }

  getRoomData(pageName: string): IRoomPage {
    return this._pagesData.pages.find(page => page.name === pageName);
  }
}
