import { AfterViewInit, ElementRef, Injectable, OnChanges, OnInit, ViewChild } from '@angular/core';
import {
  animate,
  AnimationAnimateMetadata,
  AnimationBuilder,
  AnimationStyleMetadata,
  style
} from "@angular/animations";
import { timer } from "rxjs";
import { mapTo } from "rxjs/operators";

@Injectable()
export abstract class AbstractAnimatedComponent implements AfterViewInit, OnInit, OnChanges {
  @ViewChild('animationTarget', { static: false }) el: ElementRef;

  private animation;
  private player;

  readonly ANIMATION_TIME = 390;

  protected constructor(private _builder: AnimationBuilder) { }

  ngOnChanges(): void {
    this.createdAnimation();
  }

  ngOnInit(): void {
    this.createdAnimation();
  }

  ngAfterViewInit(): void {
    this.createdAnimation();
  }

  private createdAnimation(): void {
    this.animation = this._builder.build(this.slideIn(this.ANIMATION_TIME));
    this.player = this.animation.create(this.el.nativeElement, {});
    this.player.play();
  }

  public canDeactivate() {
    this.animation = this._builder.build(this.slideOut(this.ANIMATION_TIME));
    this.player = this.animation.create(this.el.nativeElement, {});
    this.player.play();

    return timer(this.ANIMATION_TIME).pipe(mapTo(true)).toPromise();
  }

  private slideOut(
      time: number
  ): (AnimationStyleMetadata | AnimationAnimateMetadata)[] {
    return [
      style({ opacity: '1', visibility: 'visible' }),
      animate(time, style({ opacity: '0', visibility: 'hidden' })),
    ];
  }
  private slideIn(
      time: number
  ): (AnimationStyleMetadata | AnimationAnimateMetadata)[] {
    return [
      style({ opacity: '0', visibility: 'hidden' }),
      animate(time, style({ opacity: '1', visibility: 'visible'})),
    ];
  }
}
