import { Component, OnInit } from '@angular/core';
import { DataService } from "@core/services/data.service";
import { IPagesData } from "@shared/models/pages-data.model";

@Component({
  selector: 'app-images-pre-render',
  templateUrl: './images-pre-render.component.html',
  styleUrls: ['./images-pre-render.component.scss']
})
export class ImagesPreRenderComponent implements OnInit {

  readonly regExp = /src":("[^\s.]+.[\w\d]+)/gm; // get all src from json or obj

  imagesListToRender: Array<string> = [];
  pagesData: IPagesData;

  constructor(private _dataService: DataService) { }

  ngOnInit(): void {
    this.pagesData = this._dataService.pagesData;
    this.setImagesListToRender();
  }

  setImagesListToRender(): void {
    let pagesDataJson = JSON.stringify(this.pagesData);
    let imagesList = pagesDataJson.match(this.regExp);

    this.imagesListToRender = imagesList.map(image => {
      let symbols = image.split('"');

      return symbols[symbols.length - 1];
    });
  }
}
