import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-dropdown-field',
  templateUrl: './dropdown-field.component.html',
  styleUrls: ['./dropdown-field.component.scss']
})
export class DropdownFieldComponent implements OnInit {

  @Input() items: Array<string>;

  public isShowDropDown: boolean = false;

  readonly dropdownItemHeight = 26;
  readonly bottomSpace = 10;
  readonly topSpace = 15;
  readonly bottomPadding = 7;

  constructor() { }

  ngOnInit() {}

  calculateDropdownHeight(): number {
    if (!this.isShowDropDown) {
      return 0;
    }

    return this.items.length * (this.dropdownItemHeight + this.bottomSpace) + this.topSpace + this.bottomPadding;
  }

}
